export default {
	data() {
		return {
			islogin:false,
			userInfo:Object,
			baseUrl:"http://39.105.48.243:8080/crlink/"
		}
	},
	onLoad() {
		console.log("onload")
		
	},
	onShow() {
		this.getuserInfo()
	},
	methods: {
		
		getuserInfo(){
			var that = this;
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('user!ajaxGetUserInfo.action',{loginKey:loginkey}).then(res => {
				if (res.res.status == 0) {
					that.userInfo = res.inf
					that.islogin = true;
				}else{
					that.islogin = false;
					uni.removeStorageSync('loginKey');
					uni.removeStorageSync('userId');
				}
			})
		},
		login() { 
			var loginkey = uni.getStorageSync('loginKey');
			if(loginkey.length>0){
				uni.navigateTo({
					url:'./setting'
				})
			}else{
				uni.navigateTo({
					url: './login',
					success: res => {
						console.log("navitologin")
					}
				});
				
			}
			
			

		},
			
		logOut(){
			// this.islogin = false;
			// uni.removeStorageSync("loginKey");
			var that = this;
			uni.showModal({
			    title: '提示',
			    content: '确定退出',
			    success: function (res) {
			        if (res.confirm) {
						
						// uni.removeStorageaSync("loginKey");
						uni.removeStorage({
							key:"loginKey",
							success() {
									console.log("点击了退出");
								that.islogin = false;
							}
						})
					
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		
		settting(){
				var loginkey = uni.getStorageSync('loginKey');
				console.log("loginkey =" +loginkey );
				if(loginkey){
				   uni.navigateTo({
				   	url:'./setting',
					success() {
						
					}
				   })
				}else{
					uni.navigateTo({
						url:'./login'
					})
				}
		},
		
			
		watchRecord(){
			var loginkey = uni.getStorageSync('loginKey');
			console.log("loginkey =" +loginkey );
			if(loginkey){
			   uni.navigateTo({
			   	url:'./watchRecord/watchRecord',
				success() {
					
				}
			   })
			}else{
				uni.navigateTo({
					url:'./login'
				})
			}
		},
		studyRecord(){
			var loginkey = uni.getStorageSync('loginKey');
			console.log("loginkey =" +loginkey );
			if(loginkey){
			   uni.navigateTo({
			   	url:'./studyRecord/studyRecord',
				success() {
					
				}
			   })
			}else{
				uni.navigateTo({
					url:'./login'
				})
			}
		}
		
	
	}
}
